.home_content{
    grid-template-columns: 116px repeat(2,1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}
.home_social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}
.home_social-icon{
    font-size: 1.25rem;
    color: black;
}
.home_social-icon:hover{
    color: var(--title-color-dark);
}
.home_title{
    font-size:30px;
    font-style: normal;
    margin-bottom: var(--mb-0-25);
    }
.button button--flex{
    display: inline-block;
    text-decoration: none;
    color: var(--title-color);
    border: 1px solid black;
    padding: 12px 34px;
    font-size: 13px;
    background: transparent;
    position: relative;
    cursor: pointer;
}
.button button--flex:hover{
border: 1px solid black;
background: white;
transition: 1s;
}
.home_subtitle{
    position: relative;
    font-size: 25px;
    padding-left:2rem;
    
}
.home_subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--text-ccolor);
    left: 0;
    top: 1rem;
}
.home_description{
    max-width: 400px;
    margin-bottom: var(--mb-3)
}
.home_img{
    background: url(c:\Users\Keerthi\Desktop\keethika\Keerthi\DSC_5824.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 400px;
    animation: profile_animate 8s ease-in-out infinite 1s;
}
@keyframes profile_animate{
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}
@keyframes scroll{
    0%{
        transform: translateY(0);
    }
    30%{
        transform: translateY(3.75rem);
    }
}
.home_scroll-name{
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}
.home_scroll-arrow{
    font-size: 1.25rem;
    color: var(--title-color);
}
@media screen and (max-width:992px) {
    .home_content{
        grid-template-columns: 100px repeat(2,1fr);
        column-gap: 1.25rem;
    }
    .home_subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }
    .home_subtitle::before{
        width: 42px;
        top: 0.8rem;
    }
    .home_description{
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }
    .home_img{
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgb(255 255 255/30%);
    }
    .home_data{
        grid-column: 1/3;
    }
    .home_scroll{
        display: none;
    }
    }
    @media screen and (max-width:768px) {
        .home_content{
            grid-template-columns: 0.5fr 3fr;
            padding-top: 3.5rem;
        }
        .home_img{
            order: initial;
            justify-self: initial;
        }
        .home_data{
            grid-column: 1/3;
        }
        .home_img{
            box-shadow: inset 0 0 0 6px rgb(255 255 255/30%);
            width: 200px;
            height: 200px;
        }
        .home_scroll{
            display: none;
        }
    }
    @media screen and (max-width:350px) {
     .home_img{
        width: 180px;
        height: 180px;
     } 
    }